import { Capacitor } from '@capacitor/core'
import { Filesystem } from '@capacitor/filesystem'
import { File } from '@ionic-native/file'
import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { AiOutlineEye } from 'react-icons/ai'
import { TiCameraOutline } from 'react-icons/ti'
import { useHistory } from 'react-router'
import { PageApp, PhotoLoaderModalRegistroMultiple } from '../../components'
import { ModalInfo } from '../../components/ModalInfo'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP } from '../../constants'
import { comprimirImagen, useApiToaster, useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import './PerfilTransportista.css'

const FileDownload = require('js-file-download')

const moment = require('moment-timezone')

const defaultValues = {
  id: 0,
  nombre: '',
  apellido: '',
  nro_documento: '',
  email: '',
  nro_telefono: '',
  razon_social: '',
  ruc: '',
  registro_frente: null,
  registro_dorso: null,
  registro_vencimiento: '',
  antecedentesj: null,
  antecedentesp: null,
  constancia_ruc: null,
  aprobado: false,
  ciudades: [],
  imagen_perfil: '',
}

export const DocumentosTransportista: React.FC = observer(() => {
  const rootStore = useStores()
  const { transportistaStore } = rootStore
  const addToast = useToaster()
  const [Errors, setErrors] = useState(defaultValues)
  const [Loading, setLoading] = useState(false)
  const apiToaster = useApiToaster()
  const [ModalInfoContrato, setModalInfoContrato] = useState(false)
  const [Datos, setDatos] = useState<any>({})
  const [Transportista, setTranspotista] = useState(transportistaStore.transportista)
  const history = useHistory()

  useEffect(() => {
    if (transportistaStore.transportista) {
      setTranspotista(transportistaStore.transportista)
    }
  }, [transportistaStore.transportista])

  const imgSelected = async (data: any, tipo: string) => {
    if (Capacitor.getPlatform() !== 'web') {
      const contents = await Filesystem.readFile({
        path: data,
      })
      data = `data:image/jpeg;base64,${contents.data}`
    }
    let blob = await fetch(data).then((r) => r.blob())
    const imagen_comprimida = await comprimirImagen(blob, tipo)

    const datos = { ...Datos }
    // transportistaStore.transportista?.setDocumento(data, tipo)
    datos[tipo] = imagen_comprimida
    setDatos(datos)
    console.log(datos)
  }

  const guardar = () => {
    setLoading(true)
    setErrors(defaultValues)
    const datos: any = { ...Datos }
    datos['id'] = Transportista?.id
    datos['email'] = Transportista?.usuario.email
    //
    rootStore.environment.api.guardarTransportista(datos).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        addToast('Perfil actualizado exitosamente', 'success')
        transportistaStore.setTransportista(result.transportista)
      } else {
        if (result.kind === 'bad-data') {
          setErrors(result.errors)
          if (result.errors.non_field_errors) {
            addToast(result.errors.non_field_errors[0], 'warning')
          }
          setDatos({})
        } else {
          addToast('Ocurrió un error al guardar vuelva a intentar en unos momentos', 'error')
          setDatos({})
        }
      }
    })
  }

  const obtenerContrato = (tipo: string) => {
    rootStore.environment.api.getContrato(tipo).then((result) => {
      if (result.kind === 'ok') {
        if (Capacitor.getPlatform() === 'web') {
          FileDownload(result.data, 'contrato-persona-' + tipo + '.pdf')
        } else {
          const dwpath =
            Capacitor.getPlatform() === 'ios' ? `${File.documentsDirectory}` : `${File.externalRootDirectory}Download/`
          const dwpathReadable =
            Capacitor.getPlatform() === 'ios'
              ? `Documents/contrato-persona-${tipo}.pdf`
              : `Download/contrato-persona-${tipo}.pdf`

          const blobi = new Blob([result.data], { type: 'application/pdf' })

          File.writeFile(dwpath, `contrato-persona-${tipo}.pdf`, blobi, { replace: true })
            .then((res) => {
              addToast(`Contrato descargado exitosamente, guardado en ${dwpathReadable}`)
            })
            .catch((error) => {
              console.log(error)
              addToast(`Error descargando el contrato, favor intentelo nuevamente!`, 'error')
            })
        }
      } else {
        apiToaster(result, '', 'descargar contrato')
      }
    })
  }
  const aceptarContrato = () => {
    rootStore.environment.api.aceptarContrato('transportista').then((result) => {
      if (result.kind === 'ok') {
        transportistaStore.setTransportista(result.response)
      }
    })
  }
  return (
    <>
      {!!Transportista && !!transportistaStore && !!transportistaStore.transportista && (
        <PageApp
          // accion_boton_derecho={() => history.goBack()}
          // boton_derecho={<FIcon.X size={30} color={'white'} />}
          preset={'p-driver'}
          subBarraExternaHeader={
            <SubHeader links={[{ titulo: 'Configuración', ruta: RUTAS_APP.CONFIGURACION }, { titulo: 'Documentos' }]} />
          }
          titulo_header={``}
          sub_titulo={` `}
          mostrar_barra={false}
        >
          <ModalInfo
            color={'var(--naranja-transportista)'}
            isOpen={ModalInfoContrato}
            close={() => setModalInfoContrato(false)}
            titulo={'Contrato de ABi'}
            cssClass={'modal-info-contrato'}
          >
            <>
              <div className="d-flex flex-column" style={{ justifyContent: 'space-between', flex: 1 }}>
                {!!Transportista.fecha_aceptacion_contrato && (
                  <span>
                    Contrato aceptado el: {moment(Transportista.fecha_aceptacion_contrato).format('DD/MM/YYYY')}
                  </span>
                )}
                <div className="row m-1">
                  <button className="btn-principal-transportista" onClick={() => obtenerContrato('fisica')}>
                    Descargar contrato
                  </button>
                </div>
                <div className="row m-1">
                  <button className="btn-principal-transportista" onClick={() => obtenerContrato('anexo')}>
                    Descargar anexos
                  </button>
                </div>
              </div>
              {!Transportista.fecha_aceptacion_contrato && (
                <div className="row m-1">
                  <button className="btn-principal-transportista" onClick={() => aceptarContrato()}>
                    Aceptar Contrato
                  </button>
                </div>
              )}
            </>
          </ModalInfo>

          <div className="container-menu-perfil-transportista" style={{ alignItems: 'center' }}>
            <h4 style={{ textAlign: 'center', width: '100%', color: 'var(--gris-oscuro-abi)', padding: 0 }}>
              Documentos
            </h4>
            <p>
              En esta sección tenés que subir los documentos solicitados. Tenes 3 días desde el registro para
              presentarlos.
            </p>

            {Loading && <IonSpinner />}
            {!Loading && (
              <>
                <div id="documentos-transportista-config">
                  <div>
                    <div className="alert-icon-container">
                      {!!transportistaStore.documentosFaltantes?.find((doc) => doc === 'antecedentesp') && (
                        <FIcon.AlertCircle color="var(--naranja-abi)" size={20} />
                      )}
                    </div>
                    <PhotoLoaderModalRegistroMultiple
                      perfil={false}
                      color="var(--naranja-transportista)"
                      titulo_modal="Subir Antecedentes Policiales"
                      titulo_label={
                        <>
                          <TiCameraOutline size={20} />
                          <span>Certificado de Antecedentes policiales</span>
                        </>
                      }
                      imageItems={[
                        {
                          titulo: 'Certificado de Ancedentes policiales',
                          userDataLabel: 'antecedentesp',
                          preview: Transportista.antecedentesp
                            ? Transportista.antecedentesp
                            : 'assets/icon/documento.png',
                        },
                      ]}
                      infoTitulo="Antecedentes policiales"
                      infoBody={'Subí una foto del documento vigente, el mismo que presentarás en nuestra oficina.'}
                      modalInfoCssClass="info-modal-3lines"
                      on_select={imgSelected}
                      on_accept={guardar}
                    />
                  </div>
                  {Errors.antecedentesp && <span className="text-danger">{Errors.antecedentesp}</span>}
                  <div>
                    <div className="alert-icon-container">
                      {!!transportistaStore.documentosFaltantes?.find((doc) => doc === 'antecedentesj') && (
                        <FIcon.AlertCircle color="var(--naranja-abi)" size={20} />
                      )}
                    </div>
                    <PhotoLoaderModalRegistroMultiple
                      perfil={false}
                      color="var(--naranja-transportista)"
                      titulo_modal="Subir Antecedentes Judiciales"
                      infoTitulo="Antecedentes judiciales"
                      infoBody={'Subí una foto del documento vigente, el mismo que presentarás en nuestra oficina.'}
                      modalInfoCssClass="info-modal-3lines"
                      titulo_label={
                        <>
                          <TiCameraOutline size={20} />
                          <span>Certificado de Antecedentes judiciales</span>
                        </>
                      }
                      imageItems={[
                        {
                          titulo: 'Certificado de Antecedentes Judiciales',
                          userDataLabel: 'antecedentesj',
                          preview: Transportista.antecedentesj
                            ? Transportista.antecedentesj
                            : 'assets/icon/documento.png',
                        },
                      ]}
                      on_select={imgSelected}
                      on_accept={guardar}
                    />
                  </div>
                  {Errors.antecedentesj && <span className="text-danger">{Errors.antecedentesj}</span>}

                  <div>
                    <div className="alert-icon-container">
                      {!!transportistaStore.documentosFaltantes?.find((doc) => doc === 'registro_frente') && (
                        <FIcon.AlertCircle color="var(--naranja-abi)" size={20} />
                      )}
                    </div>
                    <PhotoLoaderModalRegistroMultiple
                      perfil={false}
                      color="var(--naranja-transportista)"
                      titulo_modal="Subir Registro de conducir"
                      infoTitulo="Registro de conducir"
                      infoBody={'Subí una foto vigente, del mismo que presentarás en nuestra oficina.'}
                      modalInfoCssClass="info-modal-2lines"
                      titulo_label={
                        <>
                          <TiCameraOutline size={20} />
                          <span>Registro de conducir</span>
                        </>
                      }
                      imageItems={[
                        {
                          titulo: 'Registro de conducir (frente)',
                          userDataLabel: 'registro_frente',
                          preview: Transportista.registro_frente
                            ? Transportista.registro_frente
                            : 'assets/icon/documento.png',
                        },
                        {
                          titulo: 'Registro de conducir (dorso)',
                          userDataLabel: 'registro_dorso',
                          preview: Transportista.registro_dorso
                            ? Transportista.registro_dorso
                            : 'assets/icon/documento.png',
                        },
                      ]}
                      on_select={imgSelected}
                      on_accept={guardar}
                    />
                  </div>
                  {Errors.registro_dorso && <span className="text-danger">{Errors.registro_dorso}</span>}
                  {Errors.registro_frente && <span className="text-danger">{Errors.registro_frente}</span>}

                  <div>
                    <div className="alert-icon-container">
                      {!!transportistaStore.documentosFaltantes?.find((doc) => doc === 'constancia_ruc') && (
                        <FIcon.AlertCircle color="var(--naranja-abi)" size={20} />
                      )}
                    </div>
                    <PhotoLoaderModalRegistroMultiple
                      perfil={false}
                      color="var(--naranja-transportista)"
                      titulo_modal="Subir Constancia de RUC"
                      infoTitulo="Constancia de RUC"
                      infoBody={
                        <>
                          <span>Tenés que ser contribuyente para trabajar con ABi.</span>
                          <br />
                          <span>Acá tenés que subir la constancia de RUC que emite la SET.</span>
                        </>
                      }
                      modalInfoCssClass="info-modal-4lines"
                      titulo_label={
                        <>
                          <TiCameraOutline size={20} />
                          <span>Constancia de RUC</span>
                        </>
                      }
                      imageItems={[
                        {
                          titulo: 'Constancia de RUC',
                          userDataLabel: 'constancia_ruc',
                          preview: Transportista.constancia_ruc
                            ? Transportista.constancia_ruc
                            : 'assets/icon/documento.png',
                        },
                      ]}
                      on_select={imgSelected}
                      on_accept={guardar}
                    />
                  </div>
                  {Errors.constancia_ruc && <span className="text-danger">{Errors.constancia_ruc}</span>}
                  <div>
                    <div className="alert-icon-container">
                      {Transportista.fecha_aceptacion_contrato === null && (
                        <FIcon.AlertCircle color="var(--naranja-abi)" size={20} />
                      )}
                    </div>

                    <div className="boton-photo-loader-registro-no-perfil" onClick={() => setModalInfoContrato(true)}>
                      <AiOutlineEye size={20} />
                      {!Transportista.fecha_aceptacion_contrato && <span>Aceptar contrato de ABi</span>}
                      {!!Transportista.fecha_aceptacion_contrato && <span>Contrato de ABi</span>}
                    </div>
                  </div>
                </div>
              </>
            )}
          </div>
        </PageApp>
      )}
    </>
  )
})

export default DocumentosTransportista
